//追加2
export const query = graphql`
  query {
    allMarkdownRemark(filter: {frontmatter: {isProduct: {ne: null}}},sort: { fields: [frontmatter___date], order: DESC }) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            date(formatString: "YYYY")
            title
            isProduct
            thumbnail {
              base
            }
          }
          fields {
            slug
          }
          excerpt
          parent {
            ... on File {
              relativeDirectory
            }
          }
        }
      }
    }
  }
`;